.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding:0;
    margin:2rem 5%;
  }
  
  @media (min-width:1024px) {
    .wrapper {
        margin: 1.5% 10%;
    }
  
  }
  
  @media (min-width:1520px) {
    .wrapper {
      margin: 2% 15%;
    }
  }